@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 75vh;
  text-align: center;
  background-color: #dee3e3;
  font-family: 'Roboto', sans-serif;
  color: #cfcfcf;
  font-size: 18px;
}

.main-div {
  padding: 60px;
  background-color:#232323;
}

.main-div h1 {
  margin: 0;
  margin-bottom: .5px;
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: 100;
  margin-bottom: 3rem;
}

.main-div h1 span {
  font-weight: 300;
  color: #ffe70a;
}

.secondary-div {
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
}

.input-div {
  background-color: #fff;
  display: flex;
  border-radius: 10px;
  padding: 0.6rem;
  font-size: 1.2rem;
}

input{
  border:none;
  width: 8em;
  font-size: 1.3rem;
}

select {
  border: none;
  font-size: 1.3rem;
  margin-right: 1rem;
}

.option {
  color: #555;
  margin: 2rem;
}

.icon {
  font-size: 2.5rem;
  padding: 5px 10px;
  color: #ffe70a;
  cursor: pointer;
}

select {
  margin-left: .5em;
}


@media only screen and (max-width: 780px) {


  .secondary-div {
    flex-direction: column;
  }

}
